
.header {
	grid-area: header;
	padding: 10px 15px;
	background-color: #ffffff;
	border-bottom-left-radius: 4px;
	border-bottom-right-radius: 4px;
	display: flex;
	justify-content: space-between;
	.img {
		width: 160px;
		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
}